import { Form, useLoaderData } from "remix";
import { useState, useEffect } from "react";

// Patch the country route
// Language: typescript
// Path: app\routes\country\index.tsx
// Compare this snippet from app\routes\country\index.tsx:
// Create a search bar and filter by region with remix



export async function loader({ params, request }) {
  params.cca3 = params.cca3.toLowerCase();
  const url = new URL( `https://restcountries.com/v3.1/alpha/`);
  const pais = url.searchParams.get("pais");
  const countries = await fetch(
    `https://restcountries.com/v3.1/alpha/${params.cca3}?fields=name,flags,population,region,subregion,capital,languages,tld,currencies,borders`
  );
  const country = await countries.json();
  return country;
}


export default function CountryDetails() {

  const country = useLoaderData();
  const [borders, setBorders] = useState(country.borders);
  const [loading, setLoading] = useState(true);


  return (
    <div>
      <Form reloadDocument method="get" >
        <label className="block">
         
          <input
            className="h-10"
            name="pais" 
            placeholder="Search for a country..."
          />
        </label>
      </Form>
      <img
        src={country.flags.png}
        alt={`bandeira de ${country.name.common}`}
      ></img>
      <div>
        <h2> {country.name.common}</h2>
        <ul>
          <li>
            Native Name:
            {
              Object.values(country.name.nativeName)[
                Object.values(country.name.nativeName).length - 1
              ].common
            }
          </li>
          <li>Population: {country.population}</li>
          <li>Region: {country.region}</li>
          <li>Sub Region:{country.subregion}</li>
          <li>Capital:{country.capital}</li>
          <li>Top Level Domain: {country.tld[0]}</li>
          <li>Curriencies: {Object.values(country.currencies)[0].name} </li>
          <li>
            Languages:
            {Object.values(country.languages).join(", ")}
          </li>
          <li>Border Country: {borders}</li>
        </ul>
      </div>
    </div>
  );
}
